












export default {
	data() {
		return {
			loading: false,
			mobile: ''
		}
	},
	onLoad(e) {
		if (e.mobile) {
			this.mobile = e.mobile;
		}
	},
	onShow() {},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	methods: {
		goDetailPage(url) {
			const openid = this.$store.state.openid;
			if (openid === null) {
				uni.navigateTo({
					url: '/pages/user/login/login'
				})
			} else {
				uni.navigateTo({
					url: url
				})
			}

		}
	}
}
